<template>
  <div class="container">
    <h1>Knjige</h1>
    <table class="styled-table">
      <thead>
        <tr>
          <th>Naziv</th>
          <th>Cena</th>
          <th>Odabir</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="knjiga in knjige" :key="knjiga.id">
          <td>{{ knjiga.naziv }}</td>
          <td>{{ knjiga.cijena }} RSD</td>
          <td>
            <input
              type="radio"
              name="knjiga"
              :value="knjiga.id"
              v-model="selectedKnjigaId"
            />
          </td>
        </tr>
      </tbody>
    </table>

    <h1>Oprema</h1>
    <table class="styled-table">
      <thead>
        <tr>
          <th>Naziv</th>
          <th>Odabir</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="oprema in oprema" :key="oprema.id">
          <td>{{ oprema.naziv }}</td>
          <td>
            <input
              type="radio"
              name="oprema"
              :value="oprema.id"
              v-model="selectedOpremaId"
            />
          </td>
        </tr>
      </tbody>
    </table>

    <button class="order-button" @click="naruci">Naruči</button>
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      knjige: [],
      oprema: [],
      selectedKnjigaId: null,
      selectedOpremaId: null,
      errorMessage: '',
    };
  },
  created() {
    this.fetchKnjige();
    this.fetchOprema();
  },
  methods: {
    async fetchKnjige() {
        if(!localStorage.getItem('token2')) {
        this.$router.push('/');
      }
      try {
        const response = await fetch('http://localhost:9000/knjiga');
        if (!response.ok) {
          throw new Error('Greška prilikom učitavanja knjiga.');
        }
        const data = await response.json();
        console.log('Knjige:', data);
        this.knjige = data;
      } catch (error) {
        console.error('Fetch knjige error:', error);
        this.errorMessage = error.message;
      }
    },
    async fetchOprema() {
      try {
        const response = await fetch('http://localhost:9000/oprema');
        if (!response.ok) {
          throw new Error('Greška prilikom učitavanja opreme.');
        }
        const data = await response.json();
        console.log('Oprema:', data);
        this.oprema = data;
      } catch (error) {
        console.error('Fetch oprema error:', error);
        this.errorMessage = error.message;
      }
    },
    async naruci() {
      if (!this.selectedKnjigaId) {
        this.errorMessage = 'Morate odabrati knjigu.';
        return;
      }

      this.errorMessage = '';
      const token = localStorage.getItem('token2');
      if (!token) {
        this.errorMessage = 'Nema JWT tokena u localStorage.';
        return;
      }

      const payload = JSON.parse(atob(token.split('.')[1]));
      const korisnikId = payload.id; // Assuming the user ID is stored in the token payload

      const order = {
        knjiga_id: this.selectedKnjigaId,
        dodatna_oprema_id: this.selectedOpremaId, // Can be null if not selected
        korisnik_id: korisnikId,
      };

      try {
        const response = await fetch('http://localhost:9000/narudzbina', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify(order),
        });

        if (!response.ok) {
          throw new Error('Naručivanje nije uspelo.');
        }

        alert('Narudžbina uspešno poslata!');
      } catch (error) {
        console.error('Naručivanje error:', error);
        this.errorMessage = error.message;
      }
    },
  },
};
</script>

<style scoped>
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.styled-table {
  width: 100%;
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 18px;
  font-family: 'Arial', sans-serif;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.styled-table thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: left;
}

.styled-table th,
.styled-table td {
  padding: 12px 15px;
}

.styled-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.styled-table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}

.order-button {
  background-color: #009879;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.order-button:hover {
  background-color: #007a63;
}

.error-message {
  color: red;
  font-weight: bold;
  margin-top: 20px;
}
</style>
