<template>
  <div>
    <h1>Moje Narudzbine</h1>
    <div v-if="loading">Učitavanje...</div> <!-- Show while loading -->
    <table v-else class="styled-table"> <!-- Show table after delay -->
      <thead>
        <tr>
          <th>ID Narudzbine</th>
          <th>Knjiga</th>
          <th>Oprema</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="narudzbina in filteredNarudzbine" :key="narudzbina.id">
          <td>{{ narudzbina.id }}</td>
          <td>{{ getKnjigaName(narudzbina.knjiga_id) }}</td>
          <td v-if="narudzbina.dodatna_oprema_id">{{ getOpremaName(narudzbina.dodatna_oprema_id) }}</td>
          <td v-else>Nema dodatne opreme</td>
        </tr>
      </tbody>
    </table>
    <p v-if="errorMessage" style="color: red">{{ errorMessage }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      narudzbine: [],
      knjigaNames: {},
      opremaNames: {},
      errorMessage: '',
      loading: true, // Initially set loading to true
    };
  },
  computed: {
    filteredNarudzbine() {
      const token = localStorage.getItem('token2');
      if (token) {
        const payload = JSON.parse(atob(token.split('.')[1]));
        const korisnikId = payload.id;
        return this.narudzbine.filter(narudzbina => narudzbina.korisnik_id === korisnikId);
      }
      return [];
    }
  },
  created() {
    this.fetchNarudzbine();
  },
  methods: {

    async fetchNarudzbine() {
      if(!localStorage.getItem('token2')) {
        this.$router.push('/');
      }
      try {
        const response = await fetch('http://localhost:9000/narudzbina');
        if (!response.ok) {
          throw new Error('Greska prilikom ucitavanja narudzbina.');
        }
        const data = await response.json();

        // Add a timeout before setting the data
        setTimeout(() => {
          this.narudzbine = data;
          this.fetchNames();
        }, 2000); // 2000 milliseconds = 2 seconds

      } catch (error) {
        console.error('Fetch narudzbine error:', error);
        this.errorMessage = error.message;
        this.loading = false; // Ensure loading is set to false in case of error
      }
    },
    async fetchNames() {
      try {
        const knjigeResponse = await fetch('http://localhost:9000/knjiga');
        const opremaResponse = await fetch('http://localhost:9000/oprema');

        if (knjigeResponse.ok) {
          const knjige = await knjigeResponse.json();
          this.knjigaNames = knjige.reduce((acc, knjiga) => {
            acc[knjiga.id] = knjiga.naziv;
            return acc;
          }, {});
        }

        if (opremaResponse.ok) {
          const oprema = await opremaResponse.json();
          this.opremaNames = oprema.reduce((acc, oprema) => {
            acc[oprema.id] = oprema.naziv;
            return acc;
          }, {});
        }
      } catch (error) {
        console.error('Fetch names error:', error);
        this.errorMessage = error.message;
      } finally {
        this.loading = false; // Set loading to false after fetch operations
      }
    },
    getKnjigaName(id) {
      return this.knjigaNames[id] || 'Nepoznata knjiga';
    },
    getOpremaName(id) {
      return this.opremaNames[id] || 'Nepoznata oprema';
    }
  },
};
</script>

<style>
.styled-table {
  width: 80%;
  margin: 25px auto;
  border-collapse: collapse;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.styled-table thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: left;
}

.styled-table th,
.styled-table td {
  padding: 12px 15px;
}

.styled-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.styled-table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}
</style>
